* {
    margin: 0;
    padding: 0;
    border: 0;
}

.App {
    background: #212121;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5vw;
}

h1 {
    color: #92ccd6;
    font-size: 40px;
}

.click-key-container {
    background: #171717;
    width: 20vw;
    height: 10vw;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}

.click-key {
    color: #fff;
}

.keycode-event-code {
    color: #fff;
    font-size: 6vw;
}

.box-container {
    width: 100vw;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    gap: 10vw;
}

.box {
    background: #171717;
    width: 20vw;
    height: 10vw;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    gap: 10px;
    border-radius: 10px;
}

.box-title {
    color: #fff;
    font-size: 40px;
}
